import React, { useState, useEffect } from 'react';
import { useHistory } from "react-router-dom";
import NavBar from '../components/NavBar'
import Api from "../api/api";
// import bug from "../../public/assests/bug.svg";
import bug from "../assests/bug.svg";
import chat from "../assests/gantt_chat.svg";
import map from "../assests/map.svg";
import search from "../assests/search.svg";
import noDataAttribute from "../assests/noDataAttribute.svg";


import {
    Accordion,
    AccordionItem,
    AccordionItemHeading,
    AccordionItemButton,
    AccordionItemPanel,
} from 'react-accessible-accordion';
const axios = require('axios');

function LinearAsset(props) {
    const [ganttData, setGanttData] = useState([]);
    const [ass_ganttData, setAss_ganttData] = useState([]);
    const [attributeData, setAttributeData] = useState([]);
    const [documentData, setdocumentData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [isAttrLoading, setIsAttrLoading] = useState(false);
    const [isError, setIsError] = useState(false);
    const [errMsg, setErrorMsg] = useState();
    const [showData, setShowData] = useState(false);
    const [isGanntHaveData, setIsGanntHaveData] = useState(false);
    const [isAssociateGanntHaveData, setIsAssociateGanntHaveData] = useState(false);
    const [isMapError, setIsMapError] = useState(false);
    const doApi = (searchKey) => {
        setIsLoading(true);
        //setting token in header
        axios.defaults.headers.common = { 'Authorization': localStorage.getItem("b_token") };
        Api.getRouteObj(searchKey).then((response) => {
            console.log("response ------------>");
            console.log(response);
            if (response != undefined && response != null) {
                setShowData(true);
                if (response.dataMap != undefined && response.dataMap != null && Object.keys(response.dataMap).length > 0) {
                    console.log("inside if")
                    setIsGanntHaveData(true);

                    //generating data
                    generateGanttData(response.dataMap);
                }
                else {
                    console.log("inside else")
                    //set gantt data error
                    setIsGanntHaveData(false);
                }
                if (response.dataMapEq != undefined && response.dataMapEq != null && Object.keys(response.dataMapEq).length > 0) {
                    setIsAssociateGanntHaveData(true);

                    //generate associate Data
                    generateAssociateData(response);
                }
                else {
                    //set gantt data error
                    setIsAssociateGanntHaveData(false);
                }
            }
            console.log("isGanntHaveData----------->" + isGanntHaveData)
            console.log("isAssociateGanntHaveData ------------ " + isAssociateGanntHaveData)
            setIsLoading(false);
        }).catch(error => {
            console.log("doApi error ------------>");
            console.log(error);
            setIsLoading(false);
            console.log("isGanntHaveData----------->" + isGanntHaveData)
            console.log("isAssociateGanntHaveData ------------ " + isAssociateGanntHaveData)
            if (!isAssociateGanntHaveData && !ganttData) {
                setIsError(true);
            }
            // setIsError(true);
            // setErrorMsg("Something went wrong. Please Try again!!");
        });
    };

    const generateGanttData = (LaMap) => {
        //getting y keys from main object
        let ganntKeys = Object.keys(LaMap);
        //variable to store all data
        let data = [];
        let mapdata = [];
        //generating data for fixed key
        LaMap.fixed.map((fixed_obj) => {
            let lastObj = LaMap.fixed[LaMap.fixed.length - 1];
            let pointsToAdd = 0;
            if(parseInt(lastObj.route_chainage) < 25000) {
                pointsToAdd = 300;
            }
            else{
                pointsToAdd = 1500;
            }

            //getting all keys from attrs
            let arrkeys = Object.keys(fixed_obj.refObj.attrs);

            //initalize object
            let obj = {};

            //looping attribute keys and getting attribute values
            arrkeys.map((key, index) => {
                obj[fixed_obj.refObj.attrs[key].name] = fixed_obj.refObj.attrs[key].value;
            });

            //getting lat and lang
            let lat = Number(Object.values(fixed_obj.refObj.attrs).filter(e => e.name == 'Approved Latitude')[0].value)
            // let lng = Number(Object.values(fixed_obj.refObj.attrs).filter(e => e.name == 'Approved Longitude')[0].value.replace(" E", "")) > 0 ? Number(Object.values(fixed_obj.refObj.attrs).filter(e => e.name == 'Approved Longitude')[0].value.replace(" E", "")) : Number(Object.values(fixed_obj.refObj.attrs).filter(e => e.name == 'Approved Longitude')[0].value.replace(" E", "").substring(1))
            let lng = Number(Object.values(fixed_obj.refObj.attrs).filter(e => e.name == 'Approved Longitude')[0].value)
            
            if(lat){
                mapdata.push({
                    "lat": lat ,
                    "lng": lng,
                    "ref_or_fix": "fix"
                });
            }
            
            //creating object with required values  
            data.push({
                "startDate": fixed_obj.route_chainage,
                "endDate": (fixed_obj.route_chainage + pointsToAdd),
                "taskName": "Fixed",
                "type": fixed_obj.refObj.type,
                "status": "RUNNING",
                "attrs": [{
                    "name": "Tag: ",
                    "value": fixed_obj.objId
                }],
                "hdl": fixed_obj.hdl,
                "lat": lat,
                "lng": lng
            });
        });

        //generating data for ref key
        LaMap.ref.map((ref_obj) => {
            let lastObj = LaMap.ref[LaMap.ref.length - 1];
            let pointsToAdd = 0;
            if(parseInt(lastObj.route_chainage) < 25000) {
                pointsToAdd = 300;
            }
            else{
                pointsToAdd = 1500;
            }

            //getting lat and lang
            let lat = Number(Object.values(ref_obj.refObj.attrs).filter(e => e.name == 'Approved Latitude')[0].value)
            let lng = Number(Object.values(ref_obj.refObj.attrs).filter(e => e.name == 'Approved Longitude')[0].value)
            if(lat){
                mapdata.push({
                    "lat": lat ,
                    "lng": lng,
                    "ref_or_fix": "ref"
                });
            }
            //creating object with required values
            data.push({
                "startDate": ref_obj.route_chainage,
                "endDate": (ref_obj.route_chainage + pointsToAdd),
                "taskName": "Reference",
                "type": ref_obj.refObj.type,
                "status": "SUCCEEDED",
                "attrs": [{
                    "name": "Tag: ",
                    "value": ref_obj.objId
                }],
                "hdl": ref_obj.hdl,
                "lat": lat,
                "lng": lng
            });
        });

        //generating map data
        // generateMapData(mapdata);

        //initialize map
        window.InitializeMap(mapdata);

        //set generated data to state
        setGanttData(data);

        var taskStatus = {
            "SUCCEEDED": "bar",
            "FAILED": "bar-failed",
            "RUNNING": "bar-running",
            "KILLED": "bar-killed"
        };

        var taskNames = ["Fixed", "Reference"];
        var format = "%H:%M";
        // gantt 1
        var gantt1 = window.d3.gantt("gantt", data, onClickGanttItem).taskTypes(taskNames).taskStatus(taskStatus).tickFormat(format);
        gantt1(data);
    };

    const onClickGanttItem = (clickedObj) => {
        console.log("clickedObj -------> "+JSON.stringify(clickedObj));
        setAttributeData([]);
        setdocumentData([]);
        setIsMapError(false);
        setIsAttrLoading(true);
        let objType = "TAGGED_ITEM";
        //setting token in header
        axios.defaults.headers.common = { 'Authorization': localStorage.getItem("b_token") }

        //initialize required variables
        let hdlData = [];

        //doing api call for getting attributes data
        Api.objectByHdl(clickedObj.type, clickedObj.hdl).then((response) => {
            if (response != null) {
                //getting all keys from attrs
                let arrkeys = Object.keys(response.attrs);
                //initalize object
                let obj = {};
                //looping attribute keys and getting attribute values
                arrkeys.map((key) => {
                    obj[response.attrs[key].name] = response.attrs[key].value;
                });
                //set data to state            
                setAttributeData(obj);
                setIsAttrLoading(false);
            }
        }).catch(error => {
            setIsAttrLoading(false);
            console.log("objectByHdl error---------->" + JSON.stringify(error));
        });

        //doing api call for getting document link data
        Api.objectAssociations(objType, clickedObj.hdl, "DOCUMENT").then((response) => {
            let associativeData = [];
            if (response != null) {
                if (response.items.length > 0) {
                    {
                        response.items.map((item) => {
                            associativeData.push({
                                name: item.ID,
                                class: item.ClsName,
                                link: item.Url
                            })
                        })
                    }
                    setdocumentData(associativeData);
                    setIsAttrLoading(false);
                }
            }
        }).catch(error => {
            setIsAttrLoading(false);
            console.log("objectAssociations error---------->" + JSON.stringify(error));
        });
        
        // window.myMap.setCenter({lat:clickedObj.lat, lng:clickedObj.lng})    ;
        window.myMap.setZoom(17);

        window.myMap.panTo({ lat: clickedObj.lat, lng: clickedObj.lng });
        // window.myMap.setZoom(18);      // This will trigger a zoom_changed on the map
    };

    const generateAssociateData = (AssociativeData) => {
        // console.log(JSON.stringify(AssociativeData))
        //getting y keys from main object
        let issueKeys = Object.keys(AssociativeData.dataMapEq.Issue);
        //variable to store all data
        let data = [];
        issueKeys.map((key) => {
            //generating data for fixed key
            AssociativeData.dataMapEq.Issue[key].map((issue_obj) => {
                if (issue_obj.attribute_name != "") {
                    //creating object with required values  
                    data.push({
                        "startDate": issue_obj.from_route_chainage,
                        "endDate": issue_obj.to_route_chainage,
                        "taskName": issue_obj.class_name,
                        "type": issue_obj.refObj.Type,
                        "status": "KILLED",
                        "attrs": [{
                            "name": issue_obj.attribute_name + ": ",
                            "value": issue_obj.attribute_value + " " + issue_obj.attribute_uom
                        },
                        {
                            "name": "Ref: ",
                            "value": issue_obj.refObj.ID
                        }
                        ],
                        "hdl": issue_obj.refObj.Hdl,
                    });
                }
            });
        });

        //getting y keys from main object
        let valueKeys = Object.keys(AssociativeData.dataMapEq.Value);
        //variable to store all data
        valueKeys.map((key) => {
            //generating data for fixed key
            AssociativeData.dataMapEq.Value[key].map((value_obj) => {
                if (value_obj.attribute_name != "") {
                    //creating object with required values  
                    data.push({
                        "startDate": value_obj.from_route_chainage,
                        "endDate": value_obj.to_route_chainage,
                        "taskName": value_obj.class_name,
                        "status": "RUNNING",
                        "attrs": [{
                            "name": value_obj.attribute_name + ": ",
                            "value": value_obj.attribute_value + " " + value_obj.attribute_uom
                        },
                        {
                            "name": "Ref: ",
                            "value": value_obj.refObj.ID
                        }
                        ],
                        "hdl": value_obj.refObj.Hdl,
                    });
                }
            });
        });
        //set generated associative data to state
        setAss_ganttData(data);

        var taskStatus = {
            "SUCCEEDED": "bar",
            "FAILED": "bar-failed",
            "RUNNING": "bar-running",
            "KILLED": "bar-killed"
        };
        var taskNames = [...issueKeys, ...valueKeys];
        var format = "%H:%M";
        var gantt1 = window.d3.gantt("associate_gantt", data, onClickGanttItem).taskTypes(taskNames).taskStatus(taskStatus).tickFormat(format);
        gantt1(data);
    };

    const generateMapData = (data) => {
        // console.log(JSON.stringify(data))
        let mapObj = {
            "optimizeWaypoints": false,
            "travelMode": "DRIVING"
        };
        let waypoints = [];
        {
            data.map((item, index) => {
                if (index == 0) {
                    mapObj.origin = {
                        "lat": item.lat,
                        "lng": item.lng
                    }
                }
                else if (index == (data.length - 1)) {
                    mapObj.destination = {
                        "lat": item.lat,
                        "lng": item.lng
                    }
                    mapObj.waypoints = waypoints;
                }
                else {
                    waypoints.push({
                        "location": {
                            "lat": item.lat,
                            "lng": item.lng
                        },
                        "stopover": true
                    })
                }
            })
        }
        // console.log("mapObj ------->"+JSON.stringify(mapObj));
        window.InitializeMap(mapObj);
    };

    const mapErrorCallback = (is) => {
        setIsMapError(is);
    }
    const searchClicked = (keyword) => {
        setShowData(false);
        setAttributeData([]);
        setdocumentData([]);
        doApi(keyword);
    }

    useEffect(() => {
        // window.mapErrorCallback = mapErrorCallback;
    }, []);
    return (
        <div>
            <NavBar showLogoutButton={true} showSearch={true} onClickSearch={searchClicked} ></NavBar>
            {isLoading ?
                <div className="popUpContainer">
                    <div className="loader"></div>
                </div>
                :
                ""
            }
            {isAttrLoading ?
                <div className="popUpContainer">
                    <div className="loader"></div>
                </div>
                : ""}

            {showData ?
                <div className="relative">
                    <svg style={{ height: 0, position: "absolute" }}>
                        <defs>
                            <linearGradient id="red" x1="0%" y1="0%" x2="100%" y2="0%">
                                <stop offset="0%" style={{ stopColor: "#fd3c3c", stopOpacity: 1 }} />
                                <stop offset="100%" style={{ stopColor: "#dc281e", stopOpacity: 1 }} />
                            </linearGradient>
                            <linearGradient id="green" x1="0%" y1="0%" x2="100%" y2="0%">
                                <stop offset="0%" style={{ stopColor: "#7bdb06", stopOpacity: 1 }} />
                                <stop offset="100%" style={{ stopColor: "#36ac00", stopOpacity: 1 }} />
                            </linearGradient>
                            <linearGradient id="blue" x1="0%" y1="0%" x2="100%" y2="0%">
                                <stop offset="0%" style={{ stopColor: "#00c6ff", stopOpacity: 1 }} />
                                <stop offset="100%" style={{ stopColor: "#0072ff", stopOpacity: 1 }} />
                            </linearGradient>
                            <linearGradient id="orange" x1="0%" y1="0%" x2="100%" y2="0%">
                                <stop offset="0%" style={{ stopColor: "#fe8c00", stopOpacity: 1 }} />
                                <stop offset="100%" style={{ stopColor: "#f83600", stopOpacity: 1 }} />
                            </linearGradient>
                        </defs>
                    </svg>
                    <div className="flex">
                        <div className="w-1/4 pb-5">
                            <div id="dvMap" className={isMapError ? "opacity-25" : "opacity-100 min-h-full"}></div>
                            {isMapError ?
                                <div className="min-h-full mx-auto border-dashed border border-gray-600 relative mb-5">
                                    <div className="centerItem">
                                        <img className="attr_chat mx-auto" draggable={false} src={map} />
                                        <p className="text-center mt-3 text-purple-800">Invalid map data</p>
                                    </div>
                                </div>
                                :
                                ""
                            }
                        </div>
                        <div className="w-3/4 p-5">
                            {/* gantt chat */}

                            <div id="gantt" className={isGanntHaveData ? "block" : "hidden"} style={{ minHeight: "130px" }}></div>
                            {isGanntHaveData
                                ? ""
                                :
                                <div className="h-64 mx-auto border-dashed border border-gray-600 relative mb-5">
                                    <div className="centerItem">
                                        <img className="attr_chat mx-auto" draggable={false} src={chat} />
                                        <p className="text-center mt-3 text-purple-800">No fixed and refrence data found</p>
                                    </div>
                                </div>
                            }
                            {/* associate_gantt chat*/}
                            <div id="associate_gantt" className={isAssociateGanntHaveData ? "block" : "hidden"} style={{ height: "200px" }}></div>
                            {isAssociateGanntHaveData
                                ? ""
                                :
                                <div className="h-64 mx-auto border-dashed border border-gray-600 relative mb-5">
                                    <div className="centerItem">
                                        <img className="attr_chat mx-auto" draggable={false} src={chat} />
                                        <p className="text-center mt-3 text-purple-800">No associatives data found</p>
                                    </div>
                                </div>
                            }
                            <div className="grid grid-cols-2 gap-4">
                                {/* attribute */}
                                {attributeData != undefined && attributeData != ""
                                    ?
                                    <div>
                                            {/* <p className="text-xl font-bold mb-3">Attributes</p> */}
                                            <Accordion className="border-0" preExpanded={["attributes"]} >
                                                <AccordionItem className="shadow-md mb-3" uuid="attributes"
                                                // key={item.Latitude}
                                                >
                                                    <AccordionItemHeading className="bg-gray-400 p-2">
                                                        <AccordionItemButton className="w-full border-none outline-none focus:border-0 inline-flex">
                                                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-arrow-right"><line x1="5" y1="12" x2="19" y2="12"></line><polyline points="12 5 19 12 12 19"></polyline></svg>
                                                        &emsp;
                                                        {/* {arrSplittedValue[index]} */}
                                                        Attributes
                                                    </AccordionItemButton>
                                                    </AccordionItemHeading>
                                                    <AccordionItemPanel className="h-56 overflow-y-auto">
                                                        <table class="table-auto w-full">
                                                            <tbody>
                                                                {Object.keys(attributeData).map((key) => {
                                                                    return <tr>
                                                                        <td class="border px-4 py-2">{key}</td>
                                                                        <td class="border px-4 py-2">{attributeData[key]}</td>
                                                                    </tr>
                                                                })}
                                                            </tbody>
                                                        </table>
                                                    </AccordionItemPanel>
                                                </AccordionItem>
                                            </Accordion>
                                    </div>
                                    :
                                    <div className="h-64 border-dashed border border-gray-600 relative">
                                        <div className="centerItem">
                                            <img className="attr_chat mx-auto" draggable={false} src={noDataAttribute} />
                                            <p className="text-center mt-3 text-purple-800">No attributes data found</p>
                                        </div>
                                    </div>
                                }
                                {/* document link data */}
                                {documentData != undefined && documentData.length > 0
                                    ?
                                    <div>
                                            {/* <p className="text-xl font-bold mb-3">Attributes</p> */}
                                            <Accordion className="border-0" preExpanded={["doc"]}>
                                                <AccordionItem className="shadow-md mb-3" uuid="doc"
                                                // key={item.Latitude}
                                                >
                                                    <AccordionItemHeading className="bg-gray-400 p-2">
                                                        <AccordionItemButton className="w-full border-none outline-none focus:border-0 inline-flex">
                                                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-arrow-right"><line x1="5" y1="12" x2="19" y2="12"></line><polyline points="12 5 19 12 12 19"></polyline></svg>
                                                        &emsp;
                                                        {/* {arrSplittedValue[index]} */}
                                                        Document Links
                                                    </AccordionItemButton>
                                                    </AccordionItemHeading>
                                                    <AccordionItemPanel className="h-56 overflow-y-auto">
                                                        <table class="table-auto w-full">
                                                            <tbody>
                                                                {documentData.map((item) => {
                                                                    return <tr>
                                                                        <td class="border px-4 py-2">{item.name}</td>
                                                                        <td class="border px-4 py-2">{item.class}</td>
                                                                        <td class="border px-4 py-2" >
                                                                            <a href={item.link} target="_blank">
                                                                                <svg xmlns="http://www.w3.org/2000/svg" width="17" height="17" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-link"><path d="M10 13a5 5 0 0 0 7.54.54l3-3a5 5 0 0 0-7.07-7.07l-1.72 1.71"></path><path d="M14 11a5 5 0 0 0-7.54-.54l-3 3a5 5 0 0 0 7.07 7.07l1.71-1.71"></path></svg>
                                                                            </a>
                                                                        </td>
                                                                    </tr>
                                                                })}
                                                            </tbody>
                                                        </table>
                                                    </AccordionItemPanel>
                                                </AccordionItem>
                                            </Accordion>
                                    </div>
                                    :
                                    <div className="h-64 border-dashed border border-gray-600 relative">
                                        <div className="centerItem">
                                            <img className="attr_chat mx-auto" draggable={false} src={noDataAttribute} />
                                            <p className="text-center mt-3 text-purple-800">No documents data found</p>
                                        </div>
                                    </div>
                                }
                            </div>
                        </div>
                    </div>
                </div>
                :
                <div className="centerItem">
                    <img draggable={false} width={300} src={search} />
                    <p className="text-center mt-3 text-purple-800">Search to show the data here</p>
                </div>
            }

            {isError ?
                <div className="centerItem">
                    <img draggable={false} width={300} src={bug} />
                    <p className="text-center mt-3 text-purple-800">Something went wrong. Please try again!!</p>
                </div>
                : ""}
            {/* loader */}
            {/* searchBar */}
            {/* map */}

        </div>
    )
}

export default LinearAsset;