import React, { useState, useEffect } from 'react';
import { useHistory } from "react-router-dom";
import NavBar from '../components/NavBar'
import  Api from "../api/api";


function LoginScren(props) {
  // Declare a new state variable, which we'll call "count"
  // const [count, setCount] = useState(0);
  const history = useHistory();
  const [userName, setUserName] = useState("");
  const [password, setPassword] = useState("");
  const [prefixUrl, setPrefixUrl] = useState("");
  const [isUserNameError, setIsUserNameError] = useState(false);
  const [isPassError, setIsPassError] = useState(false);
  const [isUrlError, setIsUrlError] = useState(false);
  const [isError, setisError] = useState();
  const [errMsg, setErrMsg] = useState();
  const[isLoading, setIsLoading] = useState(false);

  const inputClass = "shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:border-purple-900";
  const errInputClass = "shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none border-red-500";


  const doLogin = () => {
    setIsLoading(true);
    //reset error value
    setisError(false);
    setIsUserNameError(false);
    setIsPassError(false);
    setIsUrlError(false);
    setErrMsg("");

    // validation
    if (userName !== undefined && userName.length > 0 && password.length > 0 && prefixUrl.length > 0) {
      //setting prefix url to localstorage
      localStorage.setItem("prefixUrl", prefixUrl);
     
      Api.getToken(userName, password, prefixUrl).then((response)=>{
        if(response.data.access_token != ""){
          setIsLoading(false);
          //set token in local storage
          localStorage.setItem("b_token", response.data.token_type + " " + response.data.access_token);
          history.push('/home');
        };
      });
    }
    else {
      //setting userName error 
      if(userName.length == 0){
        setIsUserNameError(true);
      }
      //setting password error 
      if(password.length == 0){
        setIsPassError(true);
      }
      //setting userName error 
      if(prefixUrl.length == 0){
        setIsUrlError(true);
      }
      //setting common error
      setisError(true);
      setErrMsg("Please fill all the fields");
    }
  }

  return (
    <>
      <NavBar showLogoutButton={false}></NavBar>
      {isLoading ?
          <div className="popUpContainer">
              <div className="loader"></div>
          </div>
      : ""}
      <div className="w-full max-w-sm login-container mt-5">
        <form className="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4">
          <div className="mb-4">
            <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="username">
              Username
          </label>
            <input className={isUserNameError ? errInputClass : inputClass}
              id="username" type="text" value={userName} placeholder="Enter your username" onChange={(e) => setUserName(e.target.value)}
            />
          </div>
          <div className="mb-4">
            <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="password">
              Password
          </label>
            <input className={isPassError ? errInputClass : inputClass} value={password}
              id="password" type="password" placeholder="Enter your password" onChange={(e) => setPassword(e.target.value)}
            />
          </div>
          <div className="mb-3">
            <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="username">
              Prefix URL
          </label>
            <input className={isUrlError ? errInputClass : inputClass} value={prefixUrl}
              id="prefixUrl" type="text" placeholder="Enter your prefix url" onChange={(e) => setPrefixUrl(e.target.value)}
            />
            <span className="text-gray-700 text-xs">Note: Its is 1st portion of your domain name (also known as subdomain), or its called as scope of pim360.</span>
          </div>
          <p className="text-center text-red-500">{isError ? errMsg : ""}</p>
          <div className="flex">
            <button className="mt-3 bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline m-auto" type="button"
              onClick={() => { doLogin() }}>
              Proceed
      </button>
          </div>
        </form>
        <br>
        </br>
        <p className="text-center">&copy; Datum360 Limited</p>
      </div>
    </>
  );
}

export default LoginScren;