import React from 'react';
import { useHistory } from "react-router-dom";

const NavBar = props => {
    const history = useHistory();

    const logout = () => {
        //remove all local storage
        localStorage.clear();
        history.push('/');
    }

    const onClickSearch = () => {
        if(document.getElementById("search").value != ""){
            //pass value in props
            props.onClickSearch(document.getElementById("search").value)
        }
    }

    return (
        <nav className="flex items-center justify-between flex-wrap bg-purple-900  p-6">
            <div className="flex items-center flex-shrink-0 text-black mr-6">
                <img alt="Logo" className="w-1/2" src="https://www.datum360.com/assets/img/logo.png"></img>
            </div>
            <div className="block lg:hidden">
                <button className="flex items-center px-3 py-2 border rounded text-teal-200 border-teal-400 hover:text-white hover:border-white">
                    <svg className="fill-current h-3 w-3" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><title>Menu</title><path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" /></svg>
                </button>
            </div>
            <div className="w-full block flex-grow lg:flex lg:items-center lg:w-auto">
                <div className="text-sm lg:flex-grow">
                </div>
                    <div className={props.showSearch ? "relative mx-auto text-gray-600 mr-3" : "hidden"}> 
                        <input id="search" className="border-2 bg-gray-300 bg-white h-10 px-5 pr-10 rounded-lg text-sm focus:outline-none" 
                            type="search" name="search" placeholder="Enter you tag to search" 
                            defaultValue=""
                        />
                        <button type="submit" className="absolute right-0 top-0 mt-5 mr-4 r-5px t-n7px focus:outline-none" 
                            onClick={(e)=> onClickSearch(e)}
                        >
                        <svg className="text-gray-600 h-4 w-4 fill-current" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" version="1.1" id="Capa_1" x="0px" y="0px" viewBox="0 0 56.966 56.966" style={{enableBackground: 'new 0 0 56.966 56.966'}} xmlSpace="preserve" width="512px" height="512px">
                            <path d="M55.146,51.887L41.588,37.786c3.486-4.144,5.396-9.358,5.396-14.786c0-12.682-10.318-23-23-23s-23,10.318-23,23  s10.318,23,23,23c4.761,0,9.298-1.436,13.177-4.162l13.661,14.208c0.571,0.593,1.339,0.92,2.162,0.92  c0.779,0,1.518-0.297,2.079-0.837C56.255,54.982,56.293,53.08,55.146,51.887z M23.984,6c9.374,0,17,7.626,17,17s-7.626,17-17,17  s-17-7.626-17-17S14.61,6,23.984,6z" />
                        </svg>
                        </button>
                    </div>

                <div className={`${props.showLogoutButton ? "" : "hidden"}`}>
                    <button className="bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded inline-flex items-center mr-3" onClick={() => history.push("/home")}>
                        <span>Home</span>
                    </button>
                </div>
                <div className={`${props.showLogoutButton ? "" : "hidden"}`}>
                    <button className="bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded inline-flex items-center mr-3" onClick={() => history.push("/linearAsset")}>
                        <span>Linear Asset</span>
                    </button>
                </div>
                <div className={`${props.showLogoutButton ? "" : "hidden"}`}>
                    <button className="bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded inline-flex items-center" onClick={() => logout()}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-log-out"><path d="M9 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h4"></path><polyline points="16 17 21 12 16 7"></polyline><line x1="21" y1="12" x2="9" y2="12"></line></svg>
                        <span>Logout</span>
                    </button>
                </div>
            </div>
        </nav>
    );
}

export default NavBar;