import React, { useState, useEffect } from 'react';
import GoogleMapReact from 'google-map-react';
import { FileSaver, saveAs } from 'file-saver';
import { KML_GEN } from '../utils/KML_GEN'
import NavBar from '../components/NavBar'
import {
    Accordion,
    AccordionItem,
    AccordionItemHeading,
    AccordionItemButton,
    AccordionItemPanel,
} from 'react-accessible-accordion';

const AnyReactComponent = ({ text }) => <div><img alt="Marker" src="https://img.icons8.com/fluent/48/000000/marker-storm.png" />{text}</div>;

const HomeScreen = props => {
    const inputClass = "shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline";
    const errInputClass = "shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline border-red-500";
    const [isError, setIsError] = useState();
    const [inputTag, setInputTag] = useState('');
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [arrSplittedValue, setArrSplittedValue] = useState([]);

    const submitTagNumber = () => {
        //reset default value 
        setIsError(false);

        //validation
        if (inputTag !== null && inputTag.length > 0) {
            setIsLoading(true)
            let arrData = [];
            let counter = 0;
            var splitedValue = inputTag.split(",").map((item) => {
                return item.trim();
            });
            setArrSplittedValue(splitedValue);
            splitedValue.map((id) => {
                let url = "https://" + localStorage.getItem("prefixUrl") + ".pim360.io/api/objects/TAGGED_ITEM/id/" + id;
                let token = localStorage.getItem("b_token");
                fetch(url, {
                    headers: {
                        Accept: "application/json",
                        Authorization: token,
                    }
                }).then(response => {
                    var responseValue = response.json();
                    return responseValue;
                }).then(responseData => {
                    if (responseData.attrs !== undefined && responseData.attrs !== null) {
                        arrData.push(responseData);
                    }
                    else {
                        console.log(responseData);
                    }
                    counter += 1;
                    if (counter === splitedValue.length) {
                        setIsLoading(false);
                        setData(arrData);
                        return
                    }
                }).catch(error => {
                    console.log("error -------->" + error);
                    counter += 1
                    if (counter === splitedValue.length) {
                        setIsLoading(false);
                    }
                })
                return '';
            })
        }
        else {
            setIsError(true);
            console.log("show error");
        }
    }

    const showAttr = () => {
        let arrNameValue = [];
        data.map((item) => {
            let arrkeys = Object.keys(item.attrs)
            let obj = {};
            arrkeys.map((key) => {
                obj[item.attrs[key].name] = item.attrs[key].value;
                return '';
            })
            arrNameValue.push(obj);
            return '';
        });
        localStorage.setItem('pre_kml_data', JSON.stringify(arrNameValue))
        localStorage.setItem('inputsArray', JSON.stringify(arrSplittedValue))
        // console.log(JSON.stringify(arrNameValue))
        // console.log(arrSplittedValue);
        return <div className="flex mb-4">
            <div className="w-1/2 m-h-500px p-5">
                {arrNameValue.length > 0 &&
                    <div className="w-full h-full shadow-lg">
                        <GoogleMapReact
                            bootstrapURLKeys={{ key: "AIzaSyDNeF0TGdzD6PN696-ZWgdP7z9ltW0l1QY" }}
                            options={() => {
                                return {
                                    mapTypeControl: true
                                }
                            }}
                            defaultCenter={{
                                lat: parseFloat(arrNameValue[0].Latitude),
                                lng: parseFloat(arrNameValue[0].Longitude)
                            }}
                            center={{
                                lat: parseFloat(arrNameValue[0].Latitude),
                                lng: parseFloat(arrNameValue[0].Longitude)
                            }}
                            distanceToMouse={() => { }}
                            defaultZoom={arrNameValue ? 6 : 6}
                        >
                            {arrNameValue.map((nameValue) => {
                                return <AnyReactComponent
                                    lat={nameValue.Latitude}
                                    lng={nameValue.Longitude}
                                />
                            })}
                        </GoogleMapReact>
                    </div>
                }
            </div>
            <div className="w-1/2 m-h-500px">
                <div className="max-w rounded w-full shadow  p-5">
                    <p className="text-xl font-bold mb-3">Attributes</p>
                    <Accordion className="border-0" allowZeroExpanded >
                        {arrNameValue.map((item, index) => {
                            return <AccordionItem className="shadow-md mb-3" key={item.Latitude}>
                                <AccordionItemHeading className="bg-gray-400 p-2">
                                    <AccordionItemButton className="w-full border-none outline-none focus:border-0 inline-flex">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-arrow-right"><line x1="5" y1="12" x2="19" y2="12"></line><polyline points="12 5 19 12 12 19"></polyline></svg>
                                        &emsp;
                                        {arrSplittedValue[index]}
                                    </AccordionItemButton>
                                </AccordionItemHeading>
                                <AccordionItemPanel>
                                    <table class="table-auto w-full">
                                        <tbody>
                                            {Object.keys(item).map((key) => {
                                                return <tr>
                                                    <td class="border px-4 py-2">{key}</td>
                                                    <td class="border px-4 py-2">{item[key]}</td>
                                                </tr>
                                            })}
                                        </tbody>
                                    </table>
                                </AccordionItemPanel>
                            </AccordionItem>
                        })}
                    </Accordion>
                </div>
            </div>
        </div>
    }

    return (
        <>
            <NavBar showLogoutButton={true}></NavBar>
            {isLoading ?
                <div className="popUpContainer">
                    <div className="loader"></div>
                </div>
                : ""}
            <div className="p-10">
                <div className="w-full max-w">
                    <form className="bg-white rounded pt-6 pb-8 mb-4">
                        <div>
                            <div className="inline-block">
                                <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="username">
                                    Tag Number
                            </label>
                                <input className={isError ? errInputClass : inputClass} value={inputTag}
                                    id="tagNumber" type="text" placeholder="Enter your tag number" onChange={(e) => setInputTag(e.target.value)}
                                />
                            </div>
                            <div className="inline-block ml-5">
                                <button className="bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded focus:outline-none m-auto" type="button"
                                    onClick={() => { submitTagNumber() }}
                                >
                                    Submit
                             </button>
                            </div>
                            <div className="inline-block ml-5" style={{ display: data.length > 0 ? "" : "none" }}>
                                <button onClick={(e) => {
                                    e.preventDefault();
                                    // console.log()
                                    let pre_kml_data = JSON.parse(localStorage.getItem('pre_kml_data'))
                                    let inputArray = JSON.parse(localStorage.getItem('inputsArray'))
                                    var file = new File([`${KML_GEN(pre_kml_data, inputArray)}`], `${new Date()}.kml`, { type: "text/xml;charset=utf-8" });
                                    saveAs(file);
                                }} class="bg-purple-800 hover:bg-purple-900 text-white  py-2 px-4 rounded inline-flex items-center">
                                    <svg class="fill-current w-4 h-4 mr-2" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M13 8V2H7v6H2l8 8 8-8h-5zM0 18h20v2H0v-2z" /></svg>
                                    <span>Download KML</span>
                                </button>
                            </div>
                            <div className="inline-block ml-5" style={{ display: data.length > 0 ? "" : "none" }}>
                                <button onClick={(e) => {
                                    e.preventDefault();
                                    let pre_kml_data = JSON.parse(localStorage.getItem('pre_kml_data'))
                                    let json = { type: 'FeatureCollection', features: [] };
                                    let temp = pre_kml_data.map((i) => {
                                        json.features.push({
                                            type: 'Feature',
                                            geometry: {
                                                type: 'Point',
                                                coordinates: [parseFloat(i.Longitude), parseFloat(i.Latitude)],
                                            },
                                            properties: i,
                                        });
                                    });
                                    var file = new File([`${JSON.stringify(json)}`], `${new Date()}.json`, { type: "application/json;charset=utf-8" });
                                    saveAs(file);
                                }} class="bg-purple-800 hover:bg-purple-900 text-white  py-2 px-4 rounded inline-flex items-center">
                                    <svg class="fill-current w-4 h-4 mr-2" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M13 8V2H7v6H2l8 8 8-8h-5zM0 18h20v2H0v-2z" /></svg>
                                    <span>Download JSON</span>
                                </button>
                            </div>
                        </div>
                        {isError ? <p className="text-red-500 pl-2 text-xs pt-1">Tag name is required </p> : ""}
                    </form>
                </div>
                {data !== undefined && data.length > 0 &&
                    <div>
                        {showAttr()}
                    </div>
                }
                <p className="text-center">&copy; Datum360 Limited</p>
            </div>
        </>
    )
};

export default HomeScreen;