import React, { useEffect } from 'react';
import NavBar from '../components/NavBar'

function Poc(props) {
    useEffect(() => {
        var tasks = [
            {
                "startDate": 2885, "endDate": 3293, "taskName": "Hexo", "status": "KILLED", "attrs": [
                    {
                        "name": "Wear",
                        "value": "here",
                    },
                    {
                        "name": "ABC",
                        "value": "XYZ",
                    }
                ]
            },
            { "startDate": 4333, "endDate": 4453, "taskName": "Fixed", "status": "RUNNING", "tootl": "hello2" },
            { "startDate": 7025, "endDate": 9280, "taskName": "Reference", "status": "SUCCEEDED", "tootl": "hello3" },
            // { "startDate": 94, "endDate": 194, "taskName": "Reference", "status": "SUCCEEDED" },
            // { "startDate": 1000, "endDate": 1100, "taskName": "Fixed", "status": "RUNNING" },
            // { "startDate": 2000, "endDate": 2100, "taskName": "Fixed", "status": "RUNNING" },
            // { "startDate": 3200, "endDate": 3300, "taskName": "Reference", "status": "SUCCEEDED" },
        ];

        var taskStatus = {
            "SUCCEEDED": "bar",
            "FAILED": "bar-failed",
            "RUNNING": "bar-running",
            "KILLED": "bar-killed"
        };

        var taskNames = ["Hexo", "Fixed", "Reference"];

        tasks.sort(function (a, b) {
            return a.endDate - b.endDate;
        });
        var maxDate = tasks[tasks.length - 1].endDate;
        tasks.sort(function (a, b) {
            return a.startDate - b.startDate;
        });
        var minDate = tasks[0].startDate;


        var format = "%H:%M";

        // gantt 1
        var gantt1 = window.d3.gantt("new-gantt", tasks).taskTypes(taskNames).taskStatus(taskStatus).tickFormat(format);
        gantt1(tasks);


        //gantt2
        var tasks2 = [
            { "startDate": 35, "endDate": 45, "taskName": "DS - Inner Rail Wear", "status": "RUNNING", "wear": "<0.5km" },
            { "startDate": 40, "endDate": 50, "taskName": "DS - Outer Rail Wear", "status": "SUCCEEDED", "wear": "<0.1km" },
            { "startDate": 75, "endDate": 85, "taskName": "DS - Speed Restriction", "status": "FAILED", "wear": "<10km" },
            { "startDate": 100, "endDate": 110, "taskName": "DS - Vertical Clearance Limitation", "status": "RUNNING", "wear": "<3km" },
            { "startDate": 120, "endDate": 130, "taskName": "DS - Vertical Clearance Limitation", "status": "KILLED", "wear": "<5km" },
        ];


        var taskNames2 = ["DS - Inner Rail Wear", "DS - Outer Rail Wear", "DS - Speed Restriction", "DS - Vertical Clearance Limitation"];

        tasks2.sort(function (a, b) {
            return a.endDate - b.endDate;
        });


        // gantt 2
        var gantt2 = window.d3.gantt("new-gantt2", tasks2).taskTypes(taskNames2).taskStatus(taskStatus).tickFormat(format);
        gantt2(tasks2);

        // // for map
        window.InitializeMap([{ "lat": 21.2379446999999998979546944610774517059326171875, "lng": 72.77922780000000102518242783844470977783203125 }, { "lat": 21.237881200000000347927198163233697414398193359375, "lng": 72.77926089999999703650246374309062957763671875 }, { "lat": 21.23800320000000141362761496566236019134521484375, "lng": 72.779213999999996076439856551587581634521484375 }, { "lat": 21.23816790000000054305928642861545085906982421875, "lng": 72.7788641000000069425368565134704113006591796875 }]);

    }, [])
    return (
        <div>
            <NavBar showLogoutButton={true}></NavBar>
            <br></br>
            {/* <!-- // just for styling purpoe --> */}
            <svg style={{ height: 0, position: "absolute" }}>
                <defs>
                    <linearGradient id="red" x1="0%" y1="0%" x2="100%" y2="0%">
                        <stop offset="0%" style={{ stopColor: "#fd3c3c", stopOpacity: 1 }} />
                        <stop offset="100%" style={{ stopColor: "#dc281e", stopOpacity: 1 }} />
                    </linearGradient>
                    <linearGradient id="green" x1="0%" y1="0%" x2="100%" y2="0%">
                        <stop offset="0%" style={{ stopColor: "#7bdb06", stopOpacity: 1 }} />
                        <stop offset="100%" style={{ stopColor: "#36ac00", stopOpacity: 1 }} />
                    </linearGradient>
                    <linearGradient id="blue" x1="0%" y1="0%" x2="100%" y2="0%">
                        <stop offset="0%" style={{ stopColor: "#00c6ff", stopOpacity: 1 }} />
                        <stop offset="100%" style={{ stopColor: "#0072ff", stopOpacity: 1 }} />
                    </linearGradient>
                    <linearGradient id="orange" x1="0%" y1="0%" x2="100%" y2="0%">
                        <stop offset="0%" style={{ stopColor: "#fe8c00", stopOpacity: 1 }} />
                        <stop offset="100%" style={{ stopColor: "#f83600", stopOpacity: 1 }} />
                    </linearGradient>
                </defs>
            </svg>

            <h3>Gantt 1</h3>
            <div id="new-gantt" style={{ height: "130px", width: "1000px" }}></div>
            <h3>Gantt 2</h3>
            <div id="new-gantt2" style={{ height: "200px", width: "1000px" }}></div>

            <br></br>

            <h3>Map</h3>
            <br></br>
            <div id="dvMap" style={{ height: "500px", width: "35%" }}></div>
        </div>
    );
}

export default Poc;