// KML Generator\
export const KML_GEN = (geoList, inputArray) => {
    var latlngToDms = require('latlng-to-dms');

    function toDegreesMinutesAndSeconds(coordinate) {
        var absolute = Math.abs(coordinate);
        var degrees = Math.floor(absolute);
        var minutesNotTruncated = (absolute - degrees) * 60;
        var minutes = Math.floor(minutesNotTruncated);
        var seconds = Math.floor((minutesNotTruncated - minutes) * 60);
    
        return degrees + "° " + minutes + "' " + seconds + `''`;
    }

    if (geoList) {
        if (geoList.length > 0) {
            // remove space with _
            geoList.forEach(function (e, i) {
                // Iterate over the keys of object
                Object.keys(e).forEach(function (key) {

                    // Copy the value
                    var val = e[key],
                        newKey = key.replace(/\s+/g, '_');

                    // Remove key-value from object
                    delete geoList[i][key];

                    // Add value with new key
                    geoList[i][newKey] = val;
                });
            });

            // custom json to ready for xml json
            let newKML = [];
            if (geoList.length > 0) {
                geoList.map((item, index) => {
                    let new_html_for_description = Object.keys(item).map((j) => {
                        return "<div>" + j + " - " + item[j] + "<br></div>";
                    }).join('')

                    let newItem = {
                        "Placemark": {
                            "name": inputArray[index],
                            "description": `<![CDATA[${new_html_for_description}]]>`,
                            "Point": {
                                "coordinates": `${item["Longitude"]},${item["Latitude"]}`
                                // "coordinates": latlngToDms(`${item["Latitude"]}, ${item["Longitude"]}`)
                                // "coordinates": `${toDegreesMinutesAndSeconds(item["Latitude"])} ${item["Latitude"] > 0 ? "N" : "S"}, ${toDegreesMinutesAndSeconds(item["Longitude"])} ${item["Longitude"] > 0 ? "E" : "W"}`
                            }
                        }
                    };
                    newKML.push(newItem)
                })
            }

            // object to xml
            function OBJtoXML(obj) {
                var xml = '';
                for (var prop in obj) {
                    xml += obj[prop] instanceof Array ? '' : "<" + prop + ">";
                    if (obj[prop] instanceof Array) {
                        for (var array in obj[prop]) {
                            xml += "<" + prop + ">";
                            xml += OBJtoXML(new Object(obj[prop][array]));
                            xml += "</" + prop + ">";
                        }
                    } else if (typeof obj[prop] == "object") {
                        xml += OBJtoXML(new Object(obj[prop]));
                    } else {
                        xml += obj[prop];
                    }
                    xml += obj[prop] instanceof Array ? '' : "</" + prop + ">";
                }
                var xml = xml.replace(/<\/?[0-9]{1,}>/g, '');
                return xml
            }

            // formatXml
            function formatXml (xml) {
                var reg = /(>)\s*(<)(\/*)/g; // updated Mar 30, 2015
                var wsexp = / *(.*) +\n/g;
                var contexp = /(<.+>)(.+\n)/g;
                xml = xml.replace(reg, '$1\n$2$3').replace(wsexp, '$1\n').replace(contexp, '$1\n$2');
                var pad = 0;
                var formatted = '';
                var lines = xml.split('\n');
                var indent = 0;
                var lastType = 'other';
                // 4 types of tags - single, closing, opening, other (text, doctype, comment) - 4*4 = 16 transitions 
                var transitions = {
                    'single->single': 0,
                    'single->closing': -1,
                    'single->opening': 0,
                    'single->other': 0,
                    'closing->single': 0,
                    'closing->closing': -1,
                    'closing->opening': 0,
                    'closing->other': 0,
                    'opening->single': 1,
                    'opening->closing': 0,
                    'opening->opening': 1,
                    'opening->other': 1,
                    'other->single': 0,
                    'other->closing': -1,
                    'other->opening': 0,
                    'other->other': 0
                };

                for (var i = 0; i < lines.length; i++) {
                    var ln = lines[i];

                    // Luca Viggiani 2017-07-03: handle optional <?xml ... ?> declaration
                    if (ln.match(/\s*<\?xml/)) {
                        formatted += ln + "\n";
                        continue;
                    }
                    // ---

                    var single = Boolean(ln.match(/<.+\/>/)); // is this line a single tag? ex. <br />
                    var closing = Boolean(ln.match(/<\/.+>/)); // is this a closing tag? ex. </a>
                    var opening = Boolean(ln.match(/<[^!].*>/)); // is this even a tag (that's not <!something>)
                    var type = single ? 'single' : closing ? 'closing' : opening ? 'opening' : 'other';
                    var fromTo = lastType + '->' + type;
                    lastType = type;
                    var padding = '';

                    indent += transitions[fromTo];
                    for (var j = 0; j < indent; j++) {
                        padding += '\t';
                    }
                    if (fromTo == 'opening->closing')
                        formatted = formatted.substr(0, formatted.length - 1) + ln + '\n'; // substr removes line break (\n) from prev loop
                    else
                        formatted += padding + ln + '\n';
                }

                return formatted;
            };

            // final processing
            let beforeKML = OBJtoXML(newKML)
            const responseMessage = `<?xml version="1.0" encoding="UTF-8"?><kml xmlns="http://www.opengis.net/kml/2.2" xmlns:gx="http://www.google.com/kml/ext/2.2"><Document id="1">` + beforeKML + `</Document></kml>`;

            return formatXml(responseMessage);
        } else {
            return "";
        }
    } else {
        return '';
    }
}